import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/portfolio',
    name: 'Portfolio',
    component: () => import('../views/Portfolio.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior(to, savedPositon) {
    setTimeout(()=>{
      if(savedPositon) {
        return savedPositon
      } else {
        if(to.hash) {
          return {
            selector: to.hash
          }
        }
      }
      return {y: 0, x: 0}
    }, 300)
  },
  routes,
})



export default router
