<template>
  <header id="header" class="header">
    <nav v-scroll="handleScroll" class="nav">
      <div class="container flex-between">
        <router-link to="/" class="nav__logo">
          <img src="/images/Logo.png" alt />
        </router-link>
        <button :class="{'active': togglerActive}" @click="burgerHandler" class="nav__toggler">
          <span class="nav__toggler__burger"></span>
        </button>
        <div :class="{'open': menuOpen}" class="nav__menu">
          <div class="nav__link" @click="closeMenu" v-for="link in navLinks" :key="link.title">
            <router-link
              v-scroll-to="{
                el:link.hash,
                offset: link.offset
                }"
              :to="link.path"
            >{{link.title}}</router-link>
          </div>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  data: () => ({
    menuOpen: false,
    togglerActive: false,
    navLinks: [
      { offset: 0, path: "/", hash: "#home", title: "Главная" },
      { offset: -100, path: "/", hash: "#about", title: "О себе" },
      { offset: -100, path: "/", hash: "#services", title: "Услуги" },
      { offset: -100, path: "/", hash: "#comments", title: "Отзывы" },
      { offset: -100, path: "/", hash: "#contacts", title: "Контакты" },
    ],
  }),
  methods: {
    handleScroll(evt, el) {
      window.scrollY > 100
        ? el.classList.add("scroll")
        : el.classList.remove("scroll");
    },
    burgerHandler() {
      this.menuOpen = !this.menuOpen;
      this.togglerActive = !this.togglerActive;
    },
    closeMenu() {
      this.menuOpen = false;
      this.togglerActive = false;
    },
  },
};
</script>
